*{
    margin: 0;
    padding: 0;
}

.blogpost{
    border: 1px black solid;
    width:40%;
    text-align:start;
    padding: 20px;
    margin: 20px;
}

.blogpost-header{
    justify-content: flex-start;
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 30px;
}

.blogpost p{
    text-align: center;
    font-weight: 400;
}

img{
    width: 5rem;
    height: 5rem;
    border-radius: 100%;
     border: 3px  solid;
     -moz-border-top-colors: aqua;
    padding: 2px;
}

.title{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.title p,.blogpost-header p{
    font-size: 15px;
}